<template>
  <v-container
    fluid
    grid-list-md>
    <v-form
      ref="form2"
      @submit.prevent="validateBeforeSubmit">
      <core-progress-modal :show="loading" />
      <v-layout
        v-if="!loading"
        row
        wrap>
        <v-flex md12>
          <v-card class="elevation-0">
            <v-card-title>
              <h3 class="headline mb-0">Pagamento do repasse</h3>
            </v-card-title>
            <v-card-text>
              <v-layout
                row
                wrap>
                <v-flex md3>
                  <core-label-value
                    :label="'Rede'"
                    :value="value.redeNome"
                  />
                </v-flex>
                <v-flex md3>
                  <core-label-value
                    :label="'Empresa'"
                    :value="value.empresaNome"
                  />
                </v-flex>
                <v-flex md2>
                  <core-label-value
                    :label="'Vencimento até'"
                    :value="value.vencimentoAte | moment('DD/MM/YYYY')"
                  />
                </v-flex>
                <v-flex md2>
                  <core-label-value
                    :label="'Pagamento em'"
                    :value="value.pagamentoEm | moment('DD/MM/YYYY')"
                  />
                </v-flex>
                <!--
                <v-flex md3>
                  <core-label-value
                    :label="'Comissão compras (% a.m.)'"
                    :value="value.peComissaoCompra"
                  />
                </v-flex>
                -->
              </v-layout>
              <v-layout
                row
                wrap>
                <v-flex md2>
                  <core-label-value
                    :label="'Compras'"
                    :value="value.extratoRepasse.vlTotalCompras.toLocaleString('pt-BR', { minimumFractionDigits: 2 })"
                  />
                </v-flex>
                <v-flex md2>
                  <core-label-value
                    :label="'Comissão compras'"
                    :value="value.extratoRepasse.vlTotalComissaoCompras.toLocaleString('pt-BR', { minimumFractionDigits: 2 })"
                  />
                </v-flex>
                <v-flex md2>
                  <core-label-value
                    :label="'Créditos admin'"
                    :value="value.extratoRepasse.vlTotalCreditoAdmin.toLocaleString('pt-BR', { minimumFractionDigits: 2 })"
                  />
                </v-flex>
                <v-flex md2>
                  <core-label-value
                    :label="'Pagamentos loja'"
                    :value="value.extratoRepasse.vlTotalPagamentosLoja.toLocaleString('pt-BR', { minimumFractionDigits: 2 })"
                  />
                </v-flex>
                <v-flex md2>
                  <core-label-value
                    :label="'Créditos lojista'"
                    :value="value.extratoRepasse.vlTotalCreditoLojista.toLocaleString('pt-BR', { minimumFractionDigits: 2 })"
                  />
                </v-flex>
                <v-flex md2>
                  <core-label-value
                    :label="'Valor a pagar'"
                    :value="value.extratoRepasse.vlTotalPagar.toLocaleString('pt-BR', { minimumFractionDigits: 2 })"
                    :class-value="'headline font-weight-black'"
                  />
                </v-flex>
                <v-flex md2>
                  <v-text-field
                    v-model="value.depositoEm"
                    label="Depósito em"
                    type="date"
                  />
                </v-flex>
                <v-flex
                  md3
                  class="layout align-end justify-end row fill-height"
                >
                  <label
                    for="anexar"
                    style="cursor: pointer"
                    class="caption"
                    title="Anexar Comprovante">
                    {{ formData ? formData.get('name') : 'Anexar Comprovante' }}
                    <v-icon
                      color="dark"
                      for="anexar"
                    >mdi-paperclip</v-icon>
                    <input
                      id="anexar"
                      style="display: none"
                      type="file"
                      @change="anexar($event)"
                    >
                  </label>
                </v-flex>
                <v-flex md2>
                  <v-btn
                    :disabled="!value.depositoEm"
                    type="submit"
                    color="primary">Processar</v-btn>
                </v-flex>
              </v-layout>
            </v-card-text>
          </v-card>
        </v-flex>
        <v-flex md12>
          <v-layout class="justify-end">
            <v-btn
              dark
              @click="goBack">Voltar</v-btn>
          </v-layout>
        </v-flex>
        <template>
          <v-layout
            row
            justify-center>
            <v-dialog
              v-model="dialogConfirma"
              max-width="390">
              <v-card>
                <v-card-title class="headline">Confirma Processamento</v-card-title>

                <v-card-text>
                  <p>Confirma o processamento do repasse?</p>
                  <p>Após este processamento, será efetivado o repasse e não será mais possível desfazer.</p>
                </v-card-text>

                <v-card-actions>
                  <v-btn
                    outline
                    style="color: black !important"
                    @click="dialogConfirma = false">Cancelar</v-btn>

                  <v-spacer/>

                  <v-btn
                    color="primary"
                    @click="confirmaRepasse()">Confirmar</v-btn>
                </v-card-actions>
              </v-card>
            </v-dialog>
          </v-layout>
        </template>
      </v-layout>
    </v-form>
  </v-container>
</template>

<script>
import { RepasseBusiness } from '../../../business'
export default {
  props: {
    value: {
      type: Object,
      required: true
    },
    // eslint-disable-next-line vue/require-default-prop
    setData: {
      type: Function,
      requided: true
    },
    // eslint-disable-next-line vue/require-default-prop
    goBack: {
      type: Function,
      requided: true
    },
    // eslint-disable-next-line vue/require-default-prop
    nextStep: {
      type: Function
    }
  },
  data () {
    return {
      loading: false,
      file: {},
      formData: null,
      dialogConfirma: false
    }
  },
  watch: {
    value () {
      this.$emit('input', this.value)
    }
  },
  methods: {
    validateBeforeSubmit () {
      this.$validator.validate().then(result => {
        if (result) {
          this.dialogConfirma = true
        } else {
          this.$root.showAlerta(this.$vuetify.t('$vuetify.Erro.camposObrigatorios'))
        }
      })
    },
    confirmaRepasse () {
      this.dialogConfirma = false
      this.loading = true
      RepasseBusiness.processarGeral(this.value)
        .then(response => {
          this.$root.showSucesso('Repasse incluído com sucesso.')
          this.loading = false
          if (this.formData) {
            RepasseBusiness.uploadDocumento(response.data.id, this.formData)
              .catch(() => this.$root.showErro('O Repasse foi feito, mas deu erro ao enviar o comprovante de anexo, entrar em contato com o suporte'))
          }
          document.location.reload()
        })
        .catch(() => {
          this.$root.showErro('Erro ao cadastrar o Repasse, tentar mais tarde')
          this.loading = false
        })
    },
    anexar (event) {
      if (event && event.target && event.target.files.length > 0) {
        this.file = event.target.files[0]
        this.formData = new FormData()
        this.formData.append('file', this.file)
        this.formData.append('name', this.file.name)
      }
    }
  }
}
</script>
